* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    background: #f4f6f9;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .app {
    max-width: 700px;
    margin: 40px auto;
    padding: 30px;
    background: white;
    border-radius: 12px;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  h1 {
    color: #333;
    margin-bottom: 20px;
    font-size: 2.2em;
  }
  

  .input-section {
    display: flex;
    gap: 10px;
    margin-bottom: 30px;
  }
  
  .input-section input {
    flex: 1;
    padding: 14px;
    border: 2px solid #e0e0e0;
    border-radius: 8px;
    font-size: 16px;
    outline: none;
  }
  
  .input-section input:focus {
    border-color: #007bff;
  }
  
  .input-section button {
    padding: 14px 20px;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .input-section button:hover {
    background: #0056b3;
  }
  

  .todo-list {
    list-style: none;
  }
  
  .empty {
    color: #aaa;
    font-style: italic;
    margin: 20px 0;
  }
  

  .todo-item {
    background: #f8f9fa;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    margin-bottom: 10px;
    transition: all 0.3s ease;
    animation: fadeIn 0.4s ease-out;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  

  .todo-item.completed {
    animation: fadeOut 1s ease forwards;
    pointer-events: none;
  }
  
  @keyframes fadeOut {
    0% {
      opacity: 1;
      height: auto;
      margin: 10px 0;
    }
    100% {
      opacity: 0;
      height: 0;
      margin: 0;
      padding: 0;
      border: none;
    }
  }
  

  .todo-preview {
    padding: 16px 20px;
    text-align: left;
    cursor: pointer;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .todo-content {
    flex: 1;
    min-width: 0; 
  }
  
  .todo-title {
    font-size: 1.2em;
    color: #333;
    margin-bottom: 6px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  
  .todo-text-preview {
    color: #666;
    font-size: 0.95em;
    line-height: 1.4;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 100%;
  }
  

  .edit-form {
    padding: 20px;
    background: #f1f3f5;
    border-radius: 8px;
    border: 1px dashed #ccc;
  }
  
  .edit-title-input {
    width: 100%;
    padding: 16px;
    font-size: 1.2em; 
    border: 2px solid #007bff;
    border-radius: 6px;
    margin-bottom: 12px;
    outline: none;
    font-family: inherit;
  }
  
  .edit-text-input {
    width: 100%;
    height: 140px;
    padding: 16px;
    font-size: 1.1em; 
    border: 1px solid #ccc;
    border-radius: 6px;
    resize: vertical;
    outline: none;
    margin-bottom: 12px;
    font-family: inherit;
    line-height: 1.5;
  }
  
  .edit-actions {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
  }
  
  .btn-save {
    padding: 10px 16px;
    background: #28a745;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .btn-save:hover {
    background: #218838;
  }
  
  .btn-cancel {
    padding: 10px 16px;
    background: #6c757d;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .btn-cancel:hover {
    background: #5a6268;
  }
  

  .actions {
    display: flex;
    align-items: center; 
    flex-shrink: 0; 
    margin-left: 15px;
  }
  
  .btn-complete {
    padding: 8px 12px;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 14px;
  }
  
  .btn-complete:hover {
    background: #0056b3;
  }
  

  @media (max-width: 600px) {
    .app {
      margin: 20px;
      padding: 20px;
    }
  
    .input-section {
      flex-direction: column;
    }
  
    .edit-actions, .actions {
      flex-direction: column;
    }
  
    .todo-preview {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .actions {
      margin-left: 0;
      margin-top: 10px;
    }
  }